.x {
	font-size: 45px;
	font-weight: 700;
}
.calendar {
    text-align: center;
}

.Graph {
    width: 90%;
    margin: auto;
    padding: 50px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.head_text {
    text-align: center;
    padding: 10px;
}


.head_text span {
    color: rgb(105, 23, 168);
    
}
.stats img{
    width: 30%;
    
}

.stats{
    padding-bottom: 40px;
    justify-content: center;
    align-items: center;
    padding-left: 40px;
    padding-right: 40px;
}



.xx{
    background-color: rgb(71, 32, 88);
    padding-bottom: 20px;
    margin-bottom: 0%;
    font-size: 45px;
	font-weight: 700;
}

@media screen and (max-width: 768px) {
    .stats {
        flex-direction: column;
        align-items: center;

    }
}